import * as React from 'react';
import * as lodash from '@microsoft/sp-lodash-subset';

import PropertyPaneCustomFieldHost from '../propertyPaneFields/propertyPaneCustomField/PropertyPaneCustomFieldHost';
import {
  Checkbox,
  Dropdown,
  IDropdownOption,
  TextField,
  Toggle,
  Label,
  Slider,
  ChoiceGroup,
  IChoiceGroupOption,
  Button,
  Link,
  css } from 'office-ui-fabric-react';
import {
  IPropertyPaneField,
  IPropertyPaneFieldType } from '../propertyPaneFields/propertyPaneField/IPropertyPaneField';
import { IPropertyPaneChoiceGroupProps } from '../propertyPaneFields/propertyPaneChoiceGroup/IPropertyPaneChoiceGroup';
import { IPropertyPaneDropdownProps } from '../propertyPaneFields/propertyPaneDropDown/IPropertyPaneDropDown';
import { IPropertyPaneGroupProps } from './IPropertyPaneGroup';
import propertyPaneGroupStyles from './PropertyPaneGroup.module.scss';

let instance: number = 0;

/**
 * Interface for PropertyPaneGroup state.
 */
export interface IPropertyPaneGroupState {
  isDetailsShown: boolean;
  id?: string;
}

/**
 * React Component: PropertyPaneGroup.
 */
export default class PropertyPaneGroup
  extends React.Component<IPropertyPaneGroupProps, IPropertyPaneGroupState> {

  public refs: {
    [key: string]: Element,
    propertyPaneGroup: HTMLElement
  };

  /**
   * An array of keys associated with the PropertyPaneGroup fields. This array is
   * to ensure that keys are unique for the fields within the PropertyPaneGroup.
   * This resets on every render of the PropertyPaneGroup.
   *
   * @private
   * @type {{[key: string]: boolean}}
   */
  private _keys: {[key: string]: boolean};

  /**
   * constructor
   */
  public constructor(props: IPropertyPaneGroupProps) {
    super(props);

    this.state = {
      isDetailsShown: true,
      id: `PropertyPaneGroup-${instance++}`
    };

    // method bindings.
    this._onHeaderClicked = this._onHeaderClicked.bind(this);
  }

  /**
   * render: renders the PropertyPaneGroup component.
   */
  public render(): React.ReactElement<{}> {
    this._keys = {}; // Reset to ensure consistent and predictable order.

    return (
      <div>
        { this._renderGroupName() }
        { this._renderGroupContent() }
      </div>
    );
  }

  private _renderGroupName(): React.ReactElement<{}> {
    let groupNameElem: React.ReactElement<{}> = undefined;
    const rtl: boolean = document.dir === 'rtl';

    if (!this.props.displayGroupsAsAccordion) {
      if (this.props.groupName) {
        groupNameElem = (<div className={ css(
                                  propertyPaneGroupStyles.propertyPaneGroupHeaderNoAccordion,
                                  'ms-font-m',
                                  'ms-fontWeight-semibold') }
                          >
                            { this.props.groupName }
                          </div>);
      }
    } else {
      groupNameElem = (<div className={ propertyPaneGroupStyles.propertyPaneGroupHeaderWithAccordion }>
                          <button className={ css(
                                      propertyPaneGroupStyles.propertyPaneGroupHeader,
                                      'ms-font-m',
                                      'ms-fontWeight-semibold')
                                  }
                                  aria-expanded={ this.state.isDetailsShown }
                                  onClick={ this._onHeaderClicked }
                          >
                            <i className={ this.state.isDetailsShown ?
                                css('ms-Icon', 'ms-Icon--ChevronDown') :
                                css('ms-Icon', rtl ? 'ms-Icon--ChevronLeft' : 'ms-Icon--ChevronRight') }
                            ></i>
                            <div className={ propertyPaneGroupStyles.propertyPaneGroupHeaderText }>
                              { this.props.groupName }
                            </div>
                          </button>
                        </div>);
    }

    return groupNameElem;
  }

  private _renderGroupContent(): React.ReactElement<{}> {
    if ( this.state.isDetailsShown ) {
      return (
        <div className={ propertyPaneGroupStyles.propertyPaneGroupContent }>
          { this.props.groupFields.map(groupField => this._renderGroupField(groupField)) }
        </div>
      );
    }

    return undefined;
  }

  /**
   * _renderGroupField: renders the individual groupField based on the field type.
   */
  /* tslint:disable:no-any */
  private _renderGroupField<T>(groupField: IPropertyPaneField<any>): React.ReactElement<IPropertyPaneGroupProps> {
    const propValue: any = lodash.get(this.props.properties, groupField.targetProperty);
    /* tslint:enable:no-any */
    let groupFieldElement: React.ReactElement<IPropertyPaneGroupProps>;

    /**
     * React requires a key to be set for each item in the collection.
     *
     * Key should be unique and consistent. With the way PropertyPane works today, it's very difficult to ensure the
     * consistency of the keys all the time. The only way we can achieve consistency all the time is when the web part
     * developer supplies a unique key with each field. From the framework standpoint, we did not want to keep the onus
     * of sending in the unique key with the web part developers. We are keeping it simple. Hence, solving the problem
     * for most of the cases. Uniqueness is obtained by maintaining a dictionary of keys.
     *
     * Choosing 'targetProperty' as the key ensures that in most of the cases consistency is achieved. This is because
     * every field will have an associated target property. For the corner cases where there is no targetProperty or
     * same targetProperty is assigned to multiple fields, we are generating a rondom key.
     * This will result in a compromised performance, but not very noticeable and hence we are okay with it.
     *
     * If we can think of any better and simple solution to keep the consistency, we will revisit this logic.
     */
    let key: string = undefined;
    if (this._keys[groupField.targetProperty]) {
      key = groupField.targetProperty + performance.now();
    } else {
      if (groupField.targetProperty) {
        key = groupField.targetProperty;
      } else {
        key = performance.now().toString();
      }
    }
    this._keys[key] = true;

    switch (groupField.type) {

      case IPropertyPaneFieldType.Custom:
        groupFieldElement =  <PropertyPaneCustomFieldHost
          { ...groupField.properties }
        />;
      break;

      case IPropertyPaneFieldType.Toggle:
        groupFieldElement =
          <Toggle
          { ...groupField.properties }
          defaultChecked={ propValue }
          onText={ groupField.properties.onText || 'On' }
          offText={ groupField.properties.offText || 'Off' }
          onChanged={ (value: boolean): void => {
            this._onChange(groupField, value);
          }}
        />;
      break;

      case IPropertyPaneFieldType.TextField:
        groupFieldElement = <TextField
          { ...groupField.properties }
          defaultValue={ propValue }
          onChanged={ (value: string): void => {
            this._onChange(groupField, value);
          } }
          onNotifyValidationResult = { (errorMessage: string, value: string): void => {
            return this._onNotifyValidationResult(
              errorMessage,
              value,
              groupField,
              this.props.setEntryState
            );
          }}
        />;
      break;

      case IPropertyPaneFieldType.Dropdown:
        groupFieldElement = <Dropdown
          { ...groupField.properties }
          selectedKey={ propValue }
          label={ groupField.properties.label }
          onChanged={ (option: IDropdownOption, index?: number): void => {
            this._onDropDownChange(groupField, option, index);
          }}
        />;
      break;

      case IPropertyPaneFieldType.CheckBox:
        groupFieldElement = <Checkbox
          { ...groupField.properties }
          label={ groupField.properties.text }
          checked={ propValue }
          onChange={ (ev?: React.FormEvent, isChecked?: boolean): void => {
            this._onChange(groupField, isChecked);
          }}
        />;
      break;

      // (SOX VSO bug #203642)
      // Accessibility doesn't work in PropertyPane, it need change code in fabric-react.
      case IPropertyPaneFieldType.Label:
        groupFieldElement = <Label
          { ...groupField.properties }
        >
          { groupField.properties.text }
        </Label>;
      break;

      case IPropertyPaneFieldType.Slider:
        groupFieldElement =
          <Slider
            { ...groupField.properties }
            defaultValue={ propValue }
            onChange={ (value: number): void => {
              this._onChange(groupField, value);
            } }
          />;
      break;

      case IPropertyPaneFieldType.ChoiceGroup:
        groupFieldElement =
          <ChoiceGroup
            { ...groupField.properties }
            options={ groupField.properties.options }
            className={ propertyPaneGroupStyles.propertyPaneChoiceGroup }
            onChanged={ (checkedOption: IChoiceGroupOption): void => {
              this._onChoiceGroupChange(groupField, checkedOption);
            } }
          />;
      break;

      case IPropertyPaneFieldType.Button:
        const handleClick: React.MouseEventHandler = () => {
          const newValue: any = groupField.properties.onClick(propValue); // tslint:disable-line:no-any
          this._onChange(
            groupField,
            newValue
          );
        };
        groupFieldElement =
          <Button
            { ...groupField.properties }
            onClick={ handleClick }
            ariaLabel={ groupField.properties.text }
          >
              { groupField.properties.text }
          </Button>;
        break;

      case IPropertyPaneFieldType.HorizontalRule:
        groupFieldElement =
          <hr { ...groupField.properties } />;
        break;

      case IPropertyPaneFieldType.Link:
        groupFieldElement = <Link { ...groupField.properties } >
          { groupField.properties.text }
        </Link>;
        break;

      default:
        /* tslint:disable:no-null-keyword */
        groupFieldElement = null;
      /* tslint:enable:no-null-keyword */
    }

    return <div
      key={ key }
      className={ propertyPaneGroupStyles.propertyPaneGroupField }
    > { groupFieldElement } </div>;
  }

  /**
   * Event handler for a group field's onChange event.
   */
  /* tslint:disable-next-line:no-any */
  private _onChange( groupField: IPropertyPaneField<any>, value: any): void {
    if (this.props.onChange) {
      this.props.onChange(groupField.targetProperty, value);
    }

    // enable/disable Save button
    if (!groupField.properties.onGetErrorMessage) {
      this.props.setEntryState(groupField.targetProperty, true);
    }
  }

  /**
   * Event handler for a ChoiceGroup's onChange event.
   *
   * @params {IPropertyPaneField} groupField: the property pane control which has just invoked onChanged event.
   * @params {IChoiceGroupOption} checkedOption: the option representing the field which is checked now.
   */
  private _onChoiceGroupChange(
    groupField: IPropertyPaneField<IPropertyPaneChoiceGroupProps>,
    checkedOption: IChoiceGroupOption): void {
    // Maintain the choice group props before EVERYTHING happens.
    // It is needed because the design flaw of Fabric-React ChoiceGroup. Discussions in PR:
    // https://aka.ms/fociu0
    groupField.properties.options.forEach((option: IChoiceGroupOption) =>
      option.isChecked = option.key === checkedOption.key);

    this._onChange(groupField, checkedOption.key);
  }

  /**
   * Event handler for a DropDown's onChange event.
   * @todo (SPPPLAT VSO#169096) Change fabric controls to send in a common type for all the events.
   * Then change this method to use the common type.
   */
  private _onDropDownChange(
    groupField: IPropertyPaneField<IPropertyPaneDropdownProps>,
    selectedItem: IDropdownOption,
    selectedIndex: number): void {

    // invoke onPropertyChange event
    if (this.props.onChange) {
      this.props.onChange(groupField.targetProperty, selectedItem.key);
    }
  }

  /**
   * Event handler for header click.
   */
  private _onHeaderClicked(): void {
    this.setState({
      isDetailsShown: !this.state.isDetailsShown
    });
  }

  private _onNotifyValidationResult(
    errorMessage: string,
    value: string,
    groupField: IPropertyPaneField<any>, /* tslint:disable-line:no-any */
    setEntryState: (targetProperty: string, isValidEntry: boolean) => void): void {

    setEntryState(groupField.targetProperty, !errorMessage);
    if (errorMessage) {
      this._onChange(groupField, value);
    }
  }
}
